.Sidebar {
    z-index: 999;
    box-sizing: border-box;
    position : fixed;
    border-right: #eaecef;
    border-right-style: solid;
    border-right-width: 1px;
    top: 3rem;
    left: -300px;
    width: 300px;
    height: 100%;
    background: rgba(255,255, 255, 0.9);
    transition: all 500ms linear;
}
